import React from 'react';
import { IonIcon, IonButton, IonButtons, IonBackButton, IonHeader, IonAlert,
    IonSearchbar, IonPage, IonList, IonItem, IonToolbar, IonTitle, IonContent, withIonLifeCycle,
    IonCardHeader, IonCardTitle, IonCardSubtitle, IonCardContent, IonCard } from '@ionic/react';
import './Entries.css'
import { addCircle } from 'ionicons/icons';
import * as firebase from 'firebase'
import { connect } from 'react-redux';
import { IRootState } from '../store';
import _ from 'lodash'
import { Dispatch } from 'redux';
import * as asyncactions from '../store/settings/async-actions';
import { SettingsActions } from '../store/settings/types';

import * as home_asyncactions from '../store/home/async-actions';
import { HomeActions } from '../store/home/types';

const mapStateToProps = ({ settings, home }: IRootState) => {
  const { list, loading, entriesBG, settingsBG } = settings;
  const { entry, isNewEntry, selectedEntryId } = home;
  return { list, loading, entriesBG, isNewEntry, entry, selectedEntryId };
}


const mapDispatcherToProps = (dispatch: any) => {
  return {
    addItem: (item: string) => asyncactions.addItemAsync(dispatch, item),
    setFontSize: (fontSize: number) => asyncactions.setFontSizeAsync(dispatch, fontSize),
    setFontFamily: (fontFamily: string) => asyncactions.setFontFamilyAsync(dispatch, fontFamily),
    setFontColor: (fontColor: string) => asyncactions.setFontColorAsync(dispatch, fontColor),

    setIsItalic: (isItalic: boolean) => asyncactions.setIsItalicAsync(dispatch, isItalic),
    setIsBold: (isBold: boolean) => asyncactions.setIsBoldAsync(dispatch, isBold),
    setIsUnderline: (isUnderline: boolean) => asyncactions.setIsUnderlineAsync(dispatch, isUnderline),

    setHomeBG: (homeBG: string) => asyncactions.setHomeBGAsync(dispatch, homeBG),

    setText: (text: string) => home_asyncactions.setTextAsync(dispatch, text),
    selectEntry: (selectedEntry: number) => home_asyncactions.selectEntryAsync(dispatch, selectedEntry),
    updateEntry: (entry: any) => home_asyncactions.updateEntryAsync(dispatch, entry),
    setIsNewEntry: (isNewEntry: boolean) => home_asyncactions.setIsNewEntryAsync(dispatch, isNewEntry),
    setSelectedEntryId: (selectedEntryId: string) => home_asyncactions.setSelectedEntryIdAsync(dispatch, selectedEntryId),
    setSelectedMemory: (selectedMemory: object) => home_asyncactions.setSelectedMemoryAsync(dispatch, selectedMemory)

  }
}

type ReduxType = ReturnType<typeof mapStateToProps> & ReturnType<typeof mapDispatcherToProps>;


class Entries extends React.Component<ReduxType> {

  state = { closeMenu: '', allEntries: [], selectedEntry: [], showAlert: false, search: '', searchedEntries: []}

  getEntries = () => {
    firebase.database()
    .ref('entries')
    .orderByChild('userId').equalTo(_.get(firebase.auth().currentUser, "uid", ""))
    .on('value', snapshot => {
      console.log("userId entries", snapshot.val())
      var allEntries = _.map(snapshot.val(), (e: object, i: string) => _.merge(e, {id: i, allText: _.join(_.map(_.get(e, "memory"), m => _.get(m, "text")), " ") }) )
      var entries = _.orderBy(allEntries, ['createdAt'], ['desc']);
      this.setState({ allEntries : entries })
      console.log("entries", snapshot.val(), entries )

      var lastEntry = _.first(entries) || {}
      var firstEntryDate = new Date(_.get(_.last(entries), 'createdAt'))
      var lastEntryDate = new Date(_.get(lastEntry, 'createdAt'))
      var totalDays = Math.round( (lastEntryDate.getTime() - firstEntryDate.getTime())/(1000 * 3600 * 24) )

      console.log("diff dates", lastEntryDate, firstEntryDate, totalDays)
      if(new Date().toDateString() == new Date(_.get(lastEntry, "createdAt")).toDateString() && _.get(lastEntry, "memory")) {
        console.log("there is today's entry", lastEntry)
        this.props.updateEntry(_.get(lastEntry, "memory"))
        this.props.setIsNewEntry(false)

        this.props.setSelectedEntryId(_.get(lastEntry, "id", ""))

        this.updateSettings(lastEntry)
        this.props.selectEntry(0)
      } else {
        this.props.setIsNewEntry(true)
        this.props.setHomeBG("")
      }
    })
  }
  getAllEntries = () => {
    console.log("userID", _.get(firebase.auth().currentUser, "uid", ""))
    firebase.auth().onAuthStateChanged(
      (user: any) => {
        if (user) {
          this.getEntries()
        }
      })
      // .catch((err: any) => { console.log(err); })
  }

  componentDidMount = () => {
    this.getAllEntries()
    // this.getEntries()
  }

  componentWillUnmount = () => {
    if (this.props.isNewEntry) {
      firebase
        .database()
        .ref('entries')
        .push()
        .set({
          createdAt: new Date().toString(),
          updatedAt: new Date().toString(),
          memory: this.props.entry,
          userId: _.get(firebase.auth().currentUser, "uid")
        })
        .then(entry => console.log("entry is added", entry))
        .catch(err => console.log("err adding entry", err))
    } else {
      // if old entry, ask if wants to save the entry
      this.setState({ showAlert: true })
    }
  }

  imageExists = (url: string) => {
    var exists = false;
    var img = new Image();
    img.onload = function() { exists = true};
    img.onerror = function() { exists = false };
    img.src = url;
    return exists;
  }

  updateSettings = async (entry: object) => {
    this.props.setFontSize(_.get(_.first(_.get(entry, 'memory')), "textStyles.fontSize"))
    this.props.setFontFamily(_.get(_.first(_.get(entry, 'memory')), "textStyles.fontFamily"))
    this.props.setFontColor(_.get(_.first(_.get(entry, 'memory')), "textStyles.fontColor"))
    this.props.setIsItalic(_.get(_.first(_.get(entry, 'memory')), "textStyles.isItalic"))
    this.props.setIsBold(_.get(_.first(_.get(entry, 'memory')), "textStyles.isBold"))
    this.props.setIsUnderline(_.get(_.first(_.get(entry, 'memory')), "textStyles.isUnderline"));
    this.props.setText(_.get(_.first(_.get(entry, 'memory')), "text"))

    var localPath = _.get(_.first(_.get(entry, 'memory')), 'image.localPath', "aksjhd")
    var localImageExists = this.imageExists(localPath)

    if (localImageExists) {
      this.props.setHomeBG(localPath)
    } else {
      this.props.setHomeBG(_.get(_.first(_.get(entry, 'memory')), 'image.path'))
    }
  }

  entry = (entry: object, i: number) => {
    var imageUrl = _.get(_.first(_.get(entry, 'memory')), 'image.path', _.get(_.first(_.get(entry, 'memory')), 'image.localPath'))

    return <IonCard
      key={i}
      onClick={() => {
        this.setState({ selectedEntry : entry })
        this.props.setSelectedEntryId(_.get(entry, "id"))
        this.props.updateEntry(_.get(entry, "memory"))
        this.props.setIsNewEntry(false)
        this.updateSettings(entry)

        this.props.selectEntry(i)

        this.props.setSelectedMemory({})


        this.props.setSelectedMemory(entry)

        // console.log("memory", entry)
      }}
      color="light"
      style={{
        backgroundImage: `url(${imageUrl})`,
        backgroundSize: 'cover',
        backgroundPosition: 'center',
        maxHeight: 150,
        minHeight: 100,
        height: 'auto'
      }}>
        <div style={{width: '100%', height: '100%', backgroundColor: 'rgba(255, 255, 255, 0.2)'}}>
          <IonCardHeader>
            <IonCardTitle>{new Date(_.get(entry, "createdAt")).toDateString()}</IonCardTitle>
            <IonCardSubtitle color="default">Last updated at {new Date(_.get(entry, "updatedAt")).toDateString()}</IonCardSubtitle>
          </IonCardHeader>

          <IonCardContent>
            <p style={{
              color: _.get(_.first(_.get(entry, 'memory')), "textStyles.fontColor"),
              fontSize: _.get(_.first(_.get(entry, 'memory')), "textStyles.fontSize"),
              fontFamily: _.get(_.first(_.get(entry, 'memory')), "textStyles.fontFamily"),
              fontStyle: _.get(_.first(_.get(entry, 'memory')), "textStyles.isItalic") ? 'italic' : 'none',
              textDecoration: _.get(_.first(_.get(entry, 'memory')), "textStyles.isUnderline") ? 'underline' : 'none',
              fontWeight: _.get(_.first(_.get(entry, 'memory')), "textStyles.isBold") ? 'bold' : 200
            }}>{_.get(_.first(_.get(entry, 'memory')), 'text')}</p>
          </IonCardContent>
      </div>
    </IonCard>
  }

  searchEntries = (value: string) => {
    this.setState({ search: value })
    var regex = new RegExp(value)

    if (value) {
      var searchedEntries = []

      searchedEntries = _.filter(_.size(this.state.searchedEntries) ? this.state.searchedEntries : this.state.allEntries, (entry: object) => {
        // console.log("text match", _.get(entry, 'allText', ''), regex, regex.test(_.get(entry, 'allText', '')) )
        return regex.test(_.get(entry, 'allText', ''))
      })

      this.setState({ searchedEntries: searchedEntries})
    } else {
      this.setState({ searchedEntries: []})
    }
  }


  render() {
    return (
      <div id="entries" className="entry" style={{width: '100%', height: '100%', backgroundImage: `url(${this.props.entriesBG})`, backgroundSize: 'cover'}}>
        <div style={{width: '100%', height: '100%', backgroundColor: 'rgba(0, 0, 0, 0.4)'}}>
        <IonHeader>
          <IonToolbar id="search" >
            <IonSearchbar value={this.state.search} onIonChange={(value: any) => {this.searchEntries(value.detail.value)}} placeholder="Search Entries" style={{justifyContent: 'flex-start', margin: 10, padding: 0, width: '95%', height: 40, borderRadius: 5, backgroundColor: 'ivory', color: 'black'}}></IonSearchbar>
          </IonToolbar>
        </IonHeader>
        <IonContent id="entriesList">
        {
          _.map(_.size(this.state.searchedEntries) ? this.state.searchedEntries : this.state.allEntries, (entry: object, i: number) => this.entry(entry, i))
        }
        </IonContent>
        </div>
        <IonAlert
          isOpen={this.state.showAlert}
          onDidDismiss={() => this.setState({ showAlert: false})}
          header={''}
          message={'<strong>Do you want to submit the current entry?</strong>'}
          buttons={[
            {
              text: 'Cancel',
              role: 'cancel',
              cssClass: 'secondary',
              handler: blah => {
                console.log('Confirm Cancel: blah');
                this.props.updateEntry([])
              }
            },
            {
              text: 'Save',
              handler: () => {
                console.log('Confirm Okay');
                firebase
                  .database()
                  .ref(`entries/${this.props.selectedEntryId}`)
                  // .push()
                  .update({
                    // createdAt: new Date().toString(),
                    updatedAt: new Date().toString(),
                    memory: this.props.entry,
                    userId: _.get(firebase.auth().currentUser, "uid")
                  })
                  .then(entry => console.log("entry is added", entry))
                  .catch(err => console.log("err adding entry", err))
              }
            }
          ]}
        />
      </div>
    );
  }
}

export default withIonLifeCycle(connect(mapStateToProps, mapDispatcherToProps)(Entries))
