import { Constants, HomeActions, IHomeState } from './types';

const init: IHomeState = {
  loading: false,
  entry: [],
  selectedEntry: 0,
  isNewEntry: true,
  selectedEntryId: "",
  text: "",
  selectedMemory: {}
};

export function homeReducer(state: IHomeState = init, action: HomeActions): IHomeState {
  switch (action.type) {
    case Constants.SET_LOADING:
      return {...state, ...action.payload};
    case Constants.UPDATE_ENTRY:
      return {...state, ...action.payload};
    case Constants.SELECT_ENTRY:
      return {...state, ...action.payload};
    case Constants.SET_IS_NEW_ENTRY:
      return {...state, ...action.payload};
    case Constants.SELECTED_ENTRY_ID:
      return {...state, ...action.payload};
    case Constants.SET_TEXT:
      return {...state, ...action.payload};
    case Constants.SET_SELECTED_MEMORY:
      return {...state, ...action.payload};
    default:
      return state;
  }
}
