import { action } from 'typesafe-actions';
import { Constants } from './types';

export function setLoading(loading: boolean) {
    return action(Constants.SET_LOADING, {
        loading
    });
}

export function updateEntry(entry: []) {
    return action(Constants.UPDATE_ENTRY, {
        entry
    });
}

export function selectEntry(selectedEntry: number) {
    return action(Constants.SELECT_ENTRY, {
        selectedEntry
    });
}

export function setIsNewEntry(isNewEntry: boolean) {
    return action(Constants.SET_IS_NEW_ENTRY, {
        isNewEntry
    });
}

export function setSelectedEntryId(selectedEntryId: string) {
    return action(Constants.SELECTED_ENTRY_ID, {
        selectedEntryId
    });
}

export function setSelectedMemory(selectedMemory: object) {
    return action(Constants.SET_SELECTED_MEMORY, {
        selectedMemory
    });
}

export function setText(text: string) {
    return action(Constants.SET_TEXT, {
        text
    });
}
