import React from 'react';
import { Redirect, Route } from 'react-router-dom';
import { IonApp, IonRouterOutlet } from '@ionic/react';
import { IonReactRouter } from '@ionic/react-router';
import Home from './pages/Home';
import * as firebase from 'firebase';
import { Plugins, CameraResultType, CameraSource, FilesystemDirectory, FilesystemEncoding } from '@capacitor/core';
import { isPlatform } from '@ionic/react';
import _ from 'lodash';

import { settings, addCircle, images, add } from 'ionicons/icons';

import { IonIcon, IonMenuToggle, IonGrid, IonRow, IonCol, IonAlert,
IonButton, IonContent, IonHeader, IonFabButton, IonFab, IonPage, IonFooter, IonTitle, IonToolbar,IonMenu, IonList, IonItem, IonButtons, IonMenuButton, IonRippleEffect} from '@ionic/react';


/* Core CSS required for Ionic components to work properly */
import '@ionic/react/css/core.css';

/* Basic CSS for apps built with Ionic */
import '@ionic/react/css/normalize.css';
import '@ionic/react/css/structure.css';
import '@ionic/react/css/typography.css';

/* Optional CSS utils that can be commented out */
import '@ionic/react/css/padding.css';
import '@ionic/react/css/float-elements.css';
import '@ionic/react/css/text-alignment.css';
import '@ionic/react/css/text-transformation.css';
import '@ionic/react/css/flex-utils.css';
import '@ionic/react/css/display.css';
import './App.css'

/* Theme variables */
import './theme/variables.css';
import Login from './Login/Login'
import Entries from './Entries/Entries'
import Settings from './Settings/Settings'

import { connect } from 'react-redux';
import { IRootState } from './store';

import { Dispatch } from 'redux';
import * as asyncactions from './store/settings/async-actions';
import { SettingsActions } from './store/settings/types';

import * as home_asyncactions from './store/home/async-actions';
import { HomeActions } from './store/home/types';

const mapStateToProps = ({ settings, home }: IRootState) => {
  const { isAuthUser } = settings;
  const {entry, selectedEntryId, isNewEntry, selectedMemory } = home;
  return { isAuthUser, entry, selectedEntryId, isNewEntry, selectedMemory };
}


const mapDispatcherToProps = (dispatch: any) => {
  return {
    addItem: (item: string) => asyncactions.addItemAsync(dispatch, item),
    setFontSize: (fontSize: number) => asyncactions.setFontSizeAsync(dispatch, fontSize),
    setHomeBG: (homeBG: string) => asyncactions.setHomeBGAsync(dispatch, homeBG),
    setIsAuthUser: (isAuthUser: boolean) => asyncactions.setIsAuthUserAsync(dispatch, isAuthUser),
    selectEntry: (selectedEntry: number) => home_asyncactions.selectEntryAsync(dispatch, selectedEntry),
    updateEntry: (entry: any) => home_asyncactions.updateEntryAsync(dispatch, entry),
    setIsNewEntry: (isNewEntry: any) => home_asyncactions.setIsNewEntryAsync(dispatch, isNewEntry),
    setText: (text: string) => home_asyncactions.setTextAsync(dispatch, text),
    setSelectedEntryId: (selectedEntryId: string) => home_asyncactions.setSelectedEntryIdAsync(dispatch, selectedEntryId),
    setSelectedMemory: (selectedMemory: object) => home_asyncactions.setSelectedMemoryAsync(dispatch, selectedMemory)
  }
}

type ReduxType = ReturnType<typeof mapStateToProps> & ReturnType<typeof mapDispatcherToProps>;

firebase.initializeApp({
    apiKey: "AIzaSyCNJK3nAs_SEFcsMNtqp6RHkto8D37y_KQ",
    // authDomain: "tradingtastes.firebaseapp.com",
    databaseURL: "https://memorydrops-c40cb.firebaseio.com",
    projectId: "memorydrops-c40cb",
    storageBucket: "memorydrops-c40cb.appspot.com",
    // messagingSenderId: "554177034825",
})

const { Camera, Filesystem } = Plugins;


class App extends React.Component<ReduxType> {

  state = { isNewEntry: true, today: new Date().toDateString(), authUser: false, showAlert: false }

  componentDidMount = () => {
    this.checkIfLoggedIn()
  }

  checkIfLoggedIn = () => {
    firebase.auth().onAuthStateChanged(user => {
      if (user) {
        this.props.setIsAuthUser(true)
      } else {
        this.props.setIsAuthUser(false)
      }
    })
  }

  render() {
    return <>
      <IonApp>
        <IonMenu id={isPlatform('hybrid') ? "entries-menu" : ""} side="start" menuId="first" contentId="my-content">
          <Entries/>
          <IonButtons className="add-button">
            <IonFab onClick={() => {
                this.props.setIsNewEntry(true)
                this.props.updateEntry([])
                this.props.setHomeBG("")
                this.props.setSelectedEntryId("")
                this.props.setText("")
                this.props.setSelectedMemory({})
              }}
              vertical="bottom"
              horizontal="end"
              slot="fixed">
              <IonFabButton color="primary">
                {/*<h1 style={{fontSize: 60, color: 'white', fontWeight: 'bolder', marginTop: -2}}>+</h1>*/}
                <IonIcon icon={add} color="light"/>
              </IonFabButton>
            </IonFab>

          </IonButtons>
        </IonMenu>

        <IonMenu id={isPlatform('hybrid') ? "settings-menu" : ""} side="end" menuId="custom" contentId="my-content" type="push">
          <Settings/>
        </IonMenu>

        <IonPage id="my-content">

          <IonHeader id="my-header" style={{display: this.props.isAuthUser ? 'block' : 'none'}}>
            <IonToolbar id="header">
              <IonButtons slot="start">
                <IonMenuButton color="primary" autoHide={false}/>
              </IonButtons>
              <IonTitle style={{textAlign: 'center', fontSize: 25, color: 'dodgerblue', paddingLeft: 20, paddingRight: 20}}>
                {this.props.isNewEntry ? this.state.today : new Date(_.get(this.props.selectedMemory, "createdAt")).toDateString() }
              </IonTitle>
              <IonButtons slot="end">
                <IonMenuButton menu="custom" autoHide={false}>
                  <IonButton>
                    <IonIcon icon={settings} color="primary"/>
                  </IonButton>
                </IonMenuButton>
              </IonButtons>
            </IonToolbar>
          </IonHeader>

          <IonContent className="ion-padding">
            <IonReactRouter>
              <IonRouterOutlet>
                <Route path="/home" component={Home} exact={true} />
                <Route path="/login" component={Login} exact={true} />
                <Route exact path="/" render={() => <Redirect to={this.props.isAuthUser ? "/home" : "/login"} />} />
              </IonRouterOutlet>
            </IonReactRouter>
          </IonContent>
          <IonAlert
            isOpen={this.state.showAlert}
            onDidDismiss={() => this.setState({ showAlert: false})}
            header={''}
            message={'<strong>Do you want to submit the current entry?</strong>'}
            buttons={[
              {
                text: 'Cancel',
                role: 'cancel',
                cssClass: 'secondary',
                handler: blah => {
                  console.log('Confirm Cancel: blah');
                  this.props.updateEntry([])
                }
              },
              {
                text: 'Save',
                handler: () => {
                  console.log('Confirm Okay');
                  if(this.props.isNewEntry) {
                      firebase
                        .database()
                        .ref('entries')
                        .push()
                        .set({
                          createdAt: new Date().toString(),
                          updatedAt: new Date().toString(),
                          memory: this.props.entry,
                          userId: _.get(firebase.auth().currentUser, "uid")
                        })
                        .then(entry => {
                          console.log("entry is added", entry)
                          this.props.updateEntry([])
                        })
                        .catch(err => console.log("err adding entry", err))

                  } else {
                    firebase
                      .database()
                      .ref(`entries/${this.props.selectedEntryId}`)
                      .update({
                        updatedAt: new Date().toString(),
                        memory: this.props.entry,
                        userId: _.get(firebase.auth().currentUser, "uid")
                      })
                      .then(entry => {
                        console.log("entry is added", entry)
                        this.props.updateEntry([])
                      })
                      .catch(err => console.log("err adding entry", err))
                  }
                }
              }
            ]}
          />

        </IonPage>

      </IonApp>
    </>
  }
}

export default connect(mapStateToProps, mapDispatcherToProps)(App);
