import { Dispatch } from 'redux';
import * as actions from './actions';
import { HomeActions } from './types';

function sleep(timeout: number) {
    return new Promise((resolve) => setTimeout(() => resolve(), timeout));
}

export async function updateEntryAsync(dispatch: Dispatch<HomeActions>, entry: []) {
    dispatch(actions.setLoading(true));

    await sleep(1000);

    dispatch(actions.updateEntry(entry));
    dispatch(actions.setLoading(false));
}

export async function selectEntryAsync(dispatch: Dispatch<HomeActions>, selectedEntry: number) {
    dispatch(actions.setLoading(true));

    await sleep(1000);

    dispatch(actions.selectEntry(selectedEntry));
    dispatch(actions.setLoading(false));
}

