import { action } from 'typesafe-actions';
import { Constants } from './types';

export function addItemToList(item: string) {
    return action(Constants.ADD_ITEM, {
        item
    });
}

export function setLoading(loading: boolean) {
    return action(Constants.SET_LOADING, {
        loading
    });
}


export function setFontSize(fontSize: number) {
    return action(Constants.SET_FONT_SIZE, {
        fontSize
    });
}

export function setFontFamily(fontFamily: string) {
    return action(Constants.SET_FONT_FAMILY, {
        fontFamily
    });
}

export function setFontColor(fontColor: string) {
    return action(Constants.SET_FONT_COLOR, {
        fontColor
    });
}


export function setIsItalic(isItalic: boolean) {
    return action(Constants.SET_IS_ITALIC, {
        isItalic
    });
}

export function setIsBold(isBold: boolean) {
    return action(Constants.SET_IS_BOLD, {
        isBold
    });
}

export function setIsUnderline(isUnderline: boolean) {
    return action(Constants.SET_IS_UNDERLINE, {
        isUnderline
    });
}


export function setSettingsBG(settingsBG: string) {
    return action(Constants.SET_SETTINGS_BG, {
        settingsBG
    });
}

export function setEntriesBG(entriesBG: string) {
    return action(Constants.SET_ENTRIES_BG, {
        entriesBG
    });
}

export function setHomeBG(homeBG: string) {
    return action(Constants.SET_HOME_BG, {
        homeBG
    });
}

export function setIsAuthUser(isAuthUser: boolean) {
    return action(Constants.SET_IS_AUTHUSER, {
        isAuthUser
    });
}

export function setNewEntryText(newEntryText: string) {
    return action(Constants.SET_NEW_ENTRY_TEXT, {
        newEntryText
    });
}
