import React from 'react';
import { IonRange, IonIcon, IonLabel, IonGrid,
IonRow,
IonCol, IonButtons, IonPopover,
IonButton, IonSelect, IonSelectOption, IonHeader, IonSearchbar, IonPage, IonList, IonItem, IonToolbar, IonTitle, IonContent, withIonLifeCycle } from '@ionic/react';
import './Settings.css'
import { brush, arrowDropdown, arrowDropup } from 'ionicons/icons';
import { connect } from 'react-redux';
import { IRootState } from '../store';
import wallpapers from '../wallpapers'
import _ from 'lodash'
import { Dispatch } from 'redux';
import * as asyncactions from '../store/settings/async-actions';

import { SettingsActions } from '../store/settings/types';

import * as firebase from 'firebase';

const mapStateToProps = ({ settings }: IRootState) => {
  const { loading, fontSize, fontFamily, fontColor, isItalic, isBold, isUnderline, settingsBG } = settings;
  return { loading,fontSize, fontFamily, fontColor, isItalic, isBold, isUnderline, settingsBG  };
}


const mapDispatcherToProps = (dispatch: Dispatch<SettingsActions>) => {
  return {
    setFontSize: (fontSize: number) => asyncactions.setFontSizeAsync(dispatch, fontSize),
    setFontFamily: (fontFamily: string) => asyncactions.setFontFamilyAsync(dispatch, fontFamily),
    setFontColor: (fontColor: string) => asyncactions.setFontColorAsync(dispatch, fontColor),

    setIsItalic: (isItalic: boolean) => asyncactions.setIsItalicAsync(dispatch, isItalic),
    setIsBold: (isBold: boolean) => asyncactions.setIsBoldAsync(dispatch, isBold),
    setIsUnderline: (isUnderline: boolean) => asyncactions.setIsUnderlineAsync(dispatch, isUnderline),

    setHomeBG: (homeBG: string) => asyncactions.setHomeBGAsync(dispatch, homeBG),
    setSettingsBG: (settingsBG: string) => asyncactions.setSettingsBGAsync(dispatch, settingsBG),
    setEntriesBG: (entriesBG: string) => asyncactions.setEntriesBGAsync(dispatch, entriesBG),

    setIsAuthUser: (isAuthUser: boolean) => asyncactions.setIsAuthUserAsync(dispatch, isAuthUser)

  }
}

type ReduxType = ReturnType<typeof mapStateToProps> & ReturnType<typeof mapDispatcherToProps>;

class Settings extends React.Component<ReduxType> {
  state = { settingsPic: 'https://wallpaperbro.com/img/623449.jpg',
            entriesPic: '',
            textSize: 15,
            fontFamily: 'fantasy',
            fontColor: 'black',
            isBold: false,
            isItalic: false,
            isUnderline: false,
            showColors: false,
            color: 'orangered',
            showSettingPictures: false,
            showEntriesPictures: false,
            showHomePictures: false,
            showDownloadPictures: false,
            backupPictures: []}

  updateTextSize = () => {
    this.setState({ textSize: 15 })
  }

  componentDidMount = () => {
    this.getBackUpPictures()
  }

  componentWillUnmount = () => {
    // console.log("states", this.state )
  }

  getBackUpPictures = () => {
    firebase.auth().onAuthStateChanged(
      (user: any) => {
        if (user) {
          firebase.database().ref('entries')
          .orderByChild('userId').equalTo(_.get(firebase.auth().currentUser, "uid", ""))
          .once('value', snapshot => {
            var pictures = _.map(snapshot.val(), (entry: any) => {
              // console.log("backup entry", entry, _.get(entry, "memory", []))
              return _.map(_.get(entry, "memory", []), (e: any) => _.get(e, "image.path"))
            })
            var flatPictures = _.flattenDeep(pictures)
            // console.log(flatPictures)
            this.setState({ backupPictures: flatPictures })
          })
        }
      })
  }

  downloadPic = (url: string) => {
    var image = new Image();
    image.src = url
    return image
  }

  signOut = () => {
    // console.log("sign out")
    firebase.auth().signOut().then(() => this.props.setIsAuthUser(false))
  }

  renderBackgroundImages = (name: string) => {
    return <IonGrid>
      <IonRow>
    {
      wallpapers.map(image => <IonCol size="6">
      <div style={{width: '100%', borderRadius: 5, height: 100, backgroundImage: `url(${image})`, backgroundSize: 'cover'}}
        onClick={() => {
          this.setState({ [name]: image })
          if (name == 'settingsPic') {
            this.props.setSettingsBG(image)
          } else if (name == 'entriesPic') {
            this.props.setEntriesBG(image)
          } else if (name == 'homePic') {
            this.props.setHomeBG(image)
          }
        }}  >
      </div>
      </IonCol>)
    }
      </IonRow>
    </IonGrid>
  }
  render() {
    return (
      <div id="entries" className="entry" style={{width: '100%', height: '100%', backgroundImage: `url(${this.props.settingsBG})`, backgroundSize: 'cover'}}>
        <div style={{width: '100%', height: '100%', backgroundColor: 'rgba(0, 0, 0, 0.4)'}}>
        <IonHeader>
          <IonToolbar id="search" >
            <h2 style={{textAlign : 'center', color: 'black'}}>Settings</h2>
          </IonToolbar>
        </IonHeader>
        <IonContent id="entriesList" >
          <br/>
          <IonLabel style={{padding: '10px'}}>Font Size</IonLabel>
          <IonRange
            style={{paddingTop: '10px'}}
            color="secondary" min={5} max={100} step={2} pin={true}
            value={this.state.textSize}
            onIonChange={value => {
                this.setState({ textSize: value.detail.value })
                this.props.setFontSize(this.state.textSize)
                console.log("font value", value.detail.value, typeof this.state.textSize)
              }}>
            <IonIcon size="small" slot="start" icon={brush}/>
            <IonIcon slot="end" icon={brush} />
          </IonRange>

          <IonGrid>
            <IonRow>
              <IonCol size="6">
                <IonLabel>Font Family</IonLabel>
                <IonSelect style={{fontFamily: this.state.fontFamily, paddingTop: '15px'}}
                  value={this.state.fontFamily}
                  okText="Okay" cancelText="Dismiss"
                  onIonChange={value => {
                    this.setState({ fontFamily: value.detail.value })
                    this.props.setFontFamily(this.state.fontFamily)
                  }}>
                  <IonSelectOption value="fantasy">Fantasy</IonSelectOption>
                  <IonSelectOption value="cursive">Cursive</IonSelectOption>
                  <IonSelectOption value="monospace">Monospace</IonSelectOption>
                  <IonSelectOption value="san-serif">San-serif</IonSelectOption>
                  <IonSelectOption value="serif">Serif</IonSelectOption>
                </IonSelect>
              </IonCol>
              <IonCol size="6">
                <IonLabel>Font Style</IonLabel>
                <IonToolbar className="toolbar">
                  <IonButtons slot="start" style={{backgroundColor: 'azure', borderRadius: '5px'}}>
                    <IonButton style={{color: this.props.fontColor}}
                      onClick={() => {
                        this.setState({ showColors: true })
                      }}>A</IonButton>
                    <IonButton style={{fontWeight: this.props.isBold ? 'bold' : 'none'}}
                      onClick={() => {
                        let bold = !this.props.isBold
                        this.setState({ isBold: bold})
                        this.props.setIsBold(bold)
                      }}>B</IonButton>
                    <IonButton style={{fontStyle: this.props.isItalic ? 'italic' : 'normal'}}
                      onClick={() => {
                        let italic = !this.props.isItalic
                        this.setState({ isItalic: italic})
                        this.props.setIsItalic(italic)
                      }} >I</IonButton>
                    <IonButton style={{textDecoration: this.props.isUnderline ? 'underline' : 'none'}}
                      onClick={() => {
                        let underline = !this.props.isUnderline
                        this.setState({ isUnderline: underline})
                        this.props.setIsUnderline(underline)
                      }} >U</IonButton>
                  </IonButtons>
                </IonToolbar>
                <IonPopover
                  isOpen={this.state.showColors}
                  onDidDismiss={e => this.setState({ showColors: false })}
                >
                <div style={{height: '200px', paddingTop: '10px', backgroundColor: 'azure'}}>
                  <IonButtons slot="start">
                    {
                    ['aqua', 'yellow', 'magenta', 'orangered', 'chartreuse'].map(color => <IonButton
                          onClick={() => {
                            this.setState({ color: color })
                            this.props.setFontColor(color)
                          }}
                          style={{backgroundColor: color, margin: '5px', width: '40px', height: '30px'}}>
                          </IonButton>)
                    }
                  </IonButtons>
                  <IonButtons slot="start">
                    {
                    [ 'aquamarine', 'red', 'blueviolet', 'coral', 'greenyellow'].map(color => <IonButton
                          onClick={() => {
                            this.setState({ color: color })
                            this.props.setFontColor(color)
                          }}
                          style={{backgroundColor: color, margin: '5px', width: '40px', height: '30px'}}>
                          </IonButton>)
                    }
                  </IonButtons>
                  <IonButtons slot="start">
                    {
                      ['whitesmoke', 'black', 'slateblue', 'salmon', 'forestgreen'].map(color => <IonButton
                          onClick={() => {
                            this.setState({ color: color })
                            this.props.setFontColor(color)
                          }}
                          style={{backgroundColor: color, margin: '5px', width: '40px', height: '30px'}}>
                          </IonButton>)
                    }
                  </IonButtons>
                  <IonGrid>
                    <IonRow style={{padding: '0px'}}>
                      <IonCol size="6" style={{padding: '0px'}}>
                      <div style={{paddingTop: '20px', width: '100%', height: '60px', backgroundColor: 'whitesmoke', position: 'absolute'}}>
                        <IonLabel style={{marginLeft: '30%', color: this.state.color, marginTop: '30px'}}>Color</IonLabel>
                      </div>
                      </IonCol>
                      <IonCol size="6" style={{padding: '0px'}}>
                      <div style={{paddingTop: '20px', width: '100%', height: '60px', backgroundColor: 'black', position: 'absolute'}}>
                        <IonLabel style={{marginLeft: '30%', color: this.state.color, marginTop: '30px'}}>Color</IonLabel>
                      </div>
                      </IonCol>
                    </IonRow>
                  </IonGrid>
                </div>

                </IonPopover>
              </IonCol>
            </IonRow>
          </IonGrid>


          <IonItem style={{marginTop: '15px'}} className="settings-pics" onClick={() => this.setState({ showHomePictures: !this.state.showHomePictures })}>
            <IonLabel>
              Change Home Background
            </IonLabel>
            <IonIcon icon={this.state.showHomePictures ? arrowDropup : arrowDropdown}/>
          </IonItem>
          <div style={{display: this.state.showHomePictures ? 'block' : 'none', height: 400, backgroundColor: 'rgba(255, 255, 255, 0.5)', overflow: 'scroll'}}>
            {this.renderBackgroundImages('homePic')}
          </div>

          <IonItem style={{marginTop: '15px'}} className="settings-pics" onClick={() => this.setState({ showSettingPictures: !this.state.showSettingPictures })}>
            <IonLabel>
              Change Settings Background
            </IonLabel>
            <IonIcon icon={this.state.showSettingPictures ? arrowDropup : arrowDropdown}/>
          </IonItem>
          <div style={{display: this.state.showSettingPictures ? 'block' : 'none', height: 400, backgroundColor: 'rgba(255, 255, 255, 0.5)', overflowY: 'scroll'}}>
            {this.renderBackgroundImages('settingsPic')}
          </div>


          <IonItem style={{marginTop: '15px'}} className="settings-pics" onClick={() => this.setState({ showEntriesPictures: !this.state.showEntriesPictures })}>
            <IonLabel>
              Change Entries Background
            </IonLabel>
            <IonIcon icon={this.state.showEntriesPictures ? arrowDropup : arrowDropdown}/>
          </IonItem>
          <div style={{display: this.state.showEntriesPictures ? 'block' : 'none', height: 400, backgroundColor: 'rgba(255, 255, 255, 0.5)', overflowY: 'scroll'}}>
            {this.renderBackgroundImages('entriesPic')}
          </div>

          <IonItem style={{marginTop: '15px'}} className="settings-pics" onClick={() => this.setState({ showDownloadPictures: !this.state.showDownloadPictures })}>
            <IonLabel>
              Download Pictures
            </IonLabel>
          </IonItem>
          <IonPopover
            isOpen={this.state.showDownloadPictures}
            onDidDismiss={e => this.setState({ showDownloadPictures: false })}
            id="backup-pictures"
          >
            <div style={{width: '90%', height: '90%', marginTop: '5%', marginLeft: '5%'}}>
              <h1> Download images </h1>
              <IonGrid>
                <IonRow>
                  {
                    _.map(this.state.backupPictures, (pic: string) => {
                      return <IonCol size="6">

                        <a href={pic} download="custom-filename.jpg">
                          <img src={pic} style={{width: '100%', height: 100, borderRadius: 10}}/>
                        </a>
                      </IonCol>
                    })
                  }
                </IonRow>
              </IonGrid>
            </div>
          </IonPopover>

          <IonItem className="settings-pics" onClick={this.signOut} style={{marginTop: 20}}>
            <IonLabel>Sign out</IonLabel>
          </IonItem>

        </IonContent>
        </div>
      </div>
    );
  }
}

export default withIonLifeCycle(connect(mapStateToProps, mapDispatcherToProps)(Settings))
