import { Constants, SettingsActions, ISettingsState } from './types';
import wallpapers from '../../wallpapers'

const init: ISettingsState = {
  list: [],
  loading: false,
  fontSize: 18,
  fontFamily: 'cursive',
  fontColor: 'black',
  isItalic: true,
  isBold: false,
  isUnderline: false,
  settingsBG: "https://wallpaperaccess.com/full/343157.jpg",
  entriesBG: "https://wallpaperaccess.com/full/343157.jpg",
  homeBG: wallpapers[Math.floor(Math.random() * (wallpapers.length -1))],
  isAuthUser: false,
  newEntryText: ''
};

export function settingsReducer(state: ISettingsState = init, action: SettingsActions): ISettingsState {
  switch (action.type) {
    case Constants.ADD_ITEM:
      return {...state, list: [...state.list, action.payload.item]};
    case Constants.SET_LOADING:
      return {...state, ...action.payload};
    case Constants.SET_FONT_SIZE:
      return {...state, ...action.payload};
    case Constants.SET_FONT_FAMILY:
      return {...state, ...action.payload};
    case Constants.SET_FONT_COLOR:
      return {...state, ...action.payload};
    case Constants.SET_IS_ITALIC:
      return {...state, ...action.payload};
    case Constants.SET_IS_BOLD:
      return {...state, ...action.payload};
    case Constants.SET_IS_UNDERLINE:
      return {...state, ...action.payload};
    case Constants.SET_SETTINGS_BG:
      return {...state, ...action.payload};
    case Constants.SET_ENTRIES_BG:
      return {...state, ...action.payload};
    case Constants.SET_HOME_BG:
      return {...state, ...action.payload};
    case Constants.SET_IS_AUTHUSER:
      return {...state, ...action.payload};
    case Constants.SET_NEW_ENTRY_TEXT:
      return {...state, ...action.payload};
    default:
      return state;
  }
}
